import React, { PureComponent } from 'react';
import './QuoteView.css';

function cn(name) {
  return `views__QuoteView__${name}`;
}

class QuoteView extends PureComponent {
  render() {
    return (
      <div className={cn('layout')}>
        "Some random quote"
      </div>
    );
  }
}

export default QuoteView;



